<template>
  <div class="topicsList">
    <div class="list" v-if="postList.value.length">
      <TopicsItem v-for="item in postList.value" :key="item.id" :item="item" />
      <div class="loading" v-if="loading">
        <img src="../assets/loading.gif" alt="" />
      </div>
    </div>
    <el-skeleton :rows="16" v-else />
  </div>
</template>

<script>
import TopicsItem from "../components/TopicsItem.vue";
import { mapState, mapActions } from "vuex";
import debounce from "lodash/debounce";
export default {
  components: { TopicsItem },
  props: ["tab"],
  data() {
    return {
      page: 2,
      loading: false,
    };
  },
  watch: {
    tab: {
      immediate: true,
      handler(newValue) {
        this.page = 2;
        if (!newValue) {
          this.getList({ tab: "all" });
        } else {
          this.page = 2;
          this.type = "newValue";
          this.getList({ tab: newValue });
        }
      },
    },
  },
  computed: {
    ...mapState(["postList"]),
  },
  methods: {
    // 防抖
    scroll: debounce(async function () {
      const scrollHeight = document.documentElement.scrollHeight; //文档高度
      const windowHeight =
        window.innerHeight || document.documentElement.clientHeight; //窗口的高度
      const scrollTop = window.scrollY; //滚动条距离头部距离
      if (
        scrollHeight - windowHeight - scrollTop <= 100 &&
        this.postList.value.length //因为进入页面的时候 当数据没来的时候，没有滚动条 默认可以能直接触底，但是并不想触发
      ) {
        // /console.log("触底了");
        this.page++;
        this.loading = true;
        await this.getList({ tab: this.tab || "all", page: this.page });
        this.loading = false;
        this.flag = true;
      }
    }, 500),
    ...mapActions(["getList"]),
  },
  mounted() {
    window.addEventListener("scroll", this.scroll);
  },
  beforeDestroy() {
    window.removeEventListener("scroll", this.scroll);
  },
};
</script>

<style lang="less">
.topicsList {
  .loading {
    height: 80px;
    width: 80px;
    margin: 0 auto;
    img {
      width: 80px;
      height: 80px;
    }
  }
}
</style>
